a {
  text-decoration: none;
}

table {
  border: none;
}

textarea {
  resize: none;
}

code {
  background: rgb(191, 187, 187, 0.7);
}

.App {
  text-align: center;
}

.app-container {
  position: absolute;
  top: 70px;
  width: 100vw;
  height: calc(100vh - 70px);
}

.log-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  text-align: left;
}

.log-container > .log:first-child {
  margin-bottom: 10px;
  background: rgba(0,0,0,0.1);
  font-weight: bold;
}

.log > div {
  display: inline-block;
  padding: 0 10px;
  text-align: center;
}

.log {
  position: relative;
  font-size: 1.1rem;
  border-radius: 2px;
  margin: 5px;
  width: 1070px;
}

.log-title {
  font-weight: bold;
  width: 90px;
  background: rgba(0,0,0,0.3);
}

.log-date {
  width: 110px;
  background: rgba(0,0,0,0.1);
}

.log-detail {
  width: 250px;
  background: rgba(0,0,0,0.075);
}

.log-amount {
  width: 120px;
  background: rgba(0,0,0,0.05);
}

.log-edit,
.log-remove,
.log-details {
  width: 120px;
}

.log-edit a,
.log-remove a,
.log-details a {
  color: black;
}

.log-edit {
  background: aliceblue;
}

.log-remove {
  background: rgba(255,0,0,0.1);
}

.log-details {
  background: rgba(200,255,0,0.2);
}

.nav {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100vw;
  height: 70px;
  background-color: rgba(0,0,0,0.9);
  color: aliceblue;
  font-size: 1.5rem;
  
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-item {
  margin: 0 20px;
  font-family: 'Lucida Grande';
  font-weight: bold;
}

.nav-item a {
  color: aliceblue;
}

.nav-item:first-child {
  position: absolute;
  left: 10px;
}

.nav-item a:hover,
.log-edit a:hover,
.log-remove a:hover,
.log-details a:hover {
  color: red;
  text-shadow: 0.5px 0.5px red;
}

.nav-icon {
  position: absolute;
  right: 10px;
  width: 70px;
  height: 70px;
  border-radius: 100px;
  overflow: hidden;
}

.nav-icon img {
  height: 100%;
  width: auto;
  transform: translateX(-15px);
  pointer-events: none;
}

.currency-table table:first-child {
  position: absolute;
  right: 230px;
  bottom: 30px;
}

.currency-table table:last-child {
  position: absolute;
  right: 30px;
  bottom: 30px;
}

.currency-table table tr td:first-child {
  background: rgba(0,0,0,0.2);
}

.currency-table table tr td:last-child {
  background: rgba(0,0,0,0.1);
}

.single-log-container,
.edit-log-container {
  position: absolute;
  background: rgba(224, 233, 174, 0.2);
  font-size: 1.5rem;
  top: 20px;
  left: 20px;
  bottom: 20px;
  width: calc(100% - 500px);
  padding: 10px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.edit-log-container {
  width: calc(100% - 60px);
  padding-top: 60px;
}

.single-log-container > .info {
  margin: 10px;
  width: calc(100% - 20px);
  text-align: left;
}

.edit-log-container > .info {
  margin: 10px;
  text-align: left;
  width: 100%;
}

.single-log-container .key,
.edit-log-container .key {
  font-weight: bold;
  font-size: 0.9rem;
}

.single-log-container .value,
.edit-log-container .value {
  border-top: 1px solid rgb(237, 86, 86);
  background: rgba(0,0,0,0.05);
  font-size: 1.1rem;
  padding: 5px;
  width: calc(100% - 30px);
  word-wrap: break-word;
  max-height: 16vh;
  overflow: auto;
  font-family: Arial;
}

.edit-log-container .value {
  border: none;
}

.single-log-detail {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(113, 252, 192, 0.2);
}

.single-log-edit,
.single-log-delete {
  position: absolute;
  bottom: 10px;
}

.single-log-edit {
  right: 51%;
}

.single-log-delete {
  left: 51%;
}

.single-log-edit button,
.single-log-delete button,
.modal-submit button {
  width: 130px;
  height: 40px;
  border: 1px solid black;
  border-radius: 2px;
  cursor: pointer;
  font-weight: bold;
  font-size: 1.2rem;
}

.small-modal-submit button {
  border: 1px solid black;
  border-radius: 2px;
  cursor: pointer;
  width: 150px;
  height: 40px;
  margin: 0 10px;
  font-size: 1rem;
  font-weight: bold;
  background: rgb(244, 185, 164, 0.8);
}

.single-log-edit button {
  background: rgba(138, 249, 128, 0.5);
}

.single-log-delete button {
  background: rgb(252, 139, 139, 0.5);
}

.deletion-modal,
.edit-modal,
.small-modal {
  z-index: 2;
  position: fixed;
  border-radius: 3px;
  height: calc(100vh - 20px);
  top: 10px;
  left: 10px;
  right: 10px;
  text-align: center;
  background: rgba(223, 215, 215, 0.98);
}

.small-modal {
  height: 300px;
  width: 400px;
  top: calc(50vh - 150px);
  left: calc(50vw - 200px);
  background: rgb(255, 255, 0);
}

.small-modal::before {
  content: '';
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  background: rgb(52, 51, 51, 0.1);
}

.modal-title,
.small-modal-title {
  position: absolute;
  top: 40px;
  width: 100%;
  font-size: 1.8rem;
  font-weight: bold;
  color: rgb(194, 64, 64);
}

.small-modal-title {
  font-size: 1.5rem;
  top: 70px;
}

.close-modal {
  position: absolute;
  z-index: 1;
  left: 20px;
  top: 20px;
  width: 30px;
  height: 27px;
  border-radius: 10px;
  border: none;
  background: red;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
  cursor: pointer;
}

.small-modal-input {
  position: absolute;
  bottom: 110px;
  left: 90px;
}

.small-modal-input input {
  width: 220px;
  height: 30px;
}

.filter-select,
.chart-type-toggle select {
  width: 150px;
  height: 30px;
  margin: 20px 5px 10px 5px;
}

.search {
  width: 200px;
  height: 25px;
  margin: 20px 5px 10px 5px;
}

.modal-submit {
  position: absolute;
  bottom: 40px;
  left: calc(calc(100% - 130px) / 2);
}

.small-modal-submit {
  position: absolute;
  bottom: 40px;
  width: 100%;
}

.modal-submit button {
  background: rgb(248, 248, 130);
}

.modal-form-status {
  transform: translateY(83vh);
  font-weight: bold;
  color: red;
  text-align: center;
}

.misc-stats {
  width: auto;
  margin: 20px 0;
  text-align: right;
  position: relative;
}

.misc-stats > div > div {
  width: 150px;
}

.misc-stats > div {
  padding: 2px 0;
}

.misc-stats > div:nth-child(2n) {
  background: rgba(0,0,0,0.05);
}

.misc-stats > div:nth-child(2n-1) {
  background: rgba(0,0,0,0.1);
}

.misc-stats .item {
  display: inline-block;
  margin: 0 10px;
}

.misc-stats .item:first-child {
  width: 130px;
}

.add-log-form {
  margin: auto;
  width: 1070px;
  padding-top: 5px;
  height: 50px;
  background: rgba(244, 229, 229, 0.9);
  border-radius: 10px;
  border: 2px solid black;
}

.add-log-form * {
  background: rgba(244, 229, 229, 0.2);
  border: none;
}

.add-log-form div {
  display: inline-block;
}

.add-log-form button {
  width: 90px;
  height: 30px;
  font-size: 1.1rem;
  background: yellow;
  margin: 4px 0 0 10px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  color: red;
  border: 2px solid grey;
  transform: translateY(5px);
}

.error-message {
  color: red;
}

.view-id {
  position: absolute;
  right: 75px;
  width: 120px;
  text-align: right;
}

.nav .active {
  font-size: 0.7rem;
  word-wrap: break-word;
  color: yellow;
}

.add-log-form > div > div{
  margin: 0 10px;
}

.add-log-form > div {
  display: inline;
}

.add-log-form input {
  height: 20px;
  border-bottom: 1px solid black;
}

.add-log-form label {
  font-size: 0.8rem;
  position: absolute;
  transform: translate(0px, 25px);
}

.add-log-form select {
  border-bottom: 1px solid black;
  height: 25px;
  width: 100px;
}

.chart {
  width: 500px;
  height: 400px;
  display: inline-block;
  position: relative;
  border: 1px solid black;
  margin: 20px 10px;
  font-weight: bold;
  padding-top: 10px;
}

.chart:nth-child(2n-1) {
  background: rgb(237, 237, 194, 0.5);
}

.chart:nth-child(2n) {
  background: rgba(204, 230, 207, 0.5);
}

.title {
  font-weight: bold;
  font-size: 1.6rem;
  margin: 20px;
  color: coral
}

.desc {
  font-size: 1.2rem;
  font-style: italic;
  margin: 30px 60px 0 60px;
}

.flex-container {
  display: flex;
  width: 80%;
  margin: 20px auto;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.flex-item {
  width: 180px;
  height: 225px;
  border-radius: 5px 10px;
  border: 2px solid lime;
  padding: 20px 0;
  margin: 3px;

  font-size: 1.2rem;
  font-style: italic;
  font-weight: bold;
  position: relative;
  text-align: center;
}

.flex-item:nth-child(2n-1) {
  background: rgb(244, 215, 215, 0.5);
}

.flex-item:nth-child(2n) {
  background: rgba(191, 180, 180, 0.5);
}

.flex-title {
  position: absolute;
  top: 20px;
  width: 100%;
  padding-bottom: 10px;
  border-bottom: 2px solid red;
}

.flex-content {
  position: absolute;
  top: 70px;
  left: 9%;
  width: 82%;
}

.footnote {
  position: fixed;
  bottom: 10px;
  right: 10px;
  font-size: 0.7rem;
  font-style: italic;
  text-align: right;
}

.footnote > div {
  margin-top: 1px;
  width: auto;
  background: white;
}

.app-container::after {
  content: '';
  height: 20px;
  display: block;
}

.chart > .pseudo-chart {
  width: 500px;
  height: 400px;
  display: inline-block;
  position: absolute;
  transform: translateY(28px);
  font-weight: bold;
  left: 0;
  top: 0;
  z-index: -1;
}

@media only screen and (max-width: 1100px) {
  .log {
    width: 650px;
  }

  .log-edit,
  .log-remove,
  .log-details {
    width: 196px;
  }

  .currency-table table:first-child {
    font-size: 0.7rem;
    right: 10px;
    top: 20px;
  }
  
  .currency-table table:last-child {
    font-size: 0.7rem;
    right: 10px;
    bottom: 20px;
  }

  .single-log-container {
    width: calc(100% - 200px);
  }

  .add-log-form {
    width: 520px;
    height: 110px;
  }

  .add-log-form > div:last-child div:nth-last-child(1),
  .add-log-form > div:last-child div:nth-last-child(2),
  .add-log-form > div:last-child div:nth-last-child(3) {
    margin-top: 30px;
    transform: translateX(-10px);
  }

  .add-log-form > div {
    display: block;
  }

  .chart {
    width: 400px;
    height: 330px;
  }

  .nav {
    font-size: 0.9rem;
    align-items: left;
    justify-content: left;
  }

  .nav-item {
    margin: 0 2px;
    max-width: 50px;
  }

  .nav-item:nth-child(2),
  .nav-item:nth-child(3),
  .nav-item:nth-child(4) {
    transform: translateX(80px);
  }

  .view-id {
    font-size: 0.6rem;
    width: 40px;
    right: 70px;
    z-index: 99;
    text-align: center;
    color: yellow;
  }

  .nav .active {
    font-size: 0.5rem;
  }

  .chart > .pseudo-chart {
    width: 400px;
    height: 330px;
  }
}